.body {
  margin-top: 20px;
  width: 100%;

  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.profile {
  &_body {
    width: 50px;
    padding: 10px;
  }

  &_photo {
    border: lavender 2px solid;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    background-color: white;
    margin: 0 auto;
    overflow: hidden;

    img {
      width: 50px;
    }
  }

  &_name {
    color: white;
    text-align: center;
  }
}
